<template>
    <div class="star" :class="starType">
        <span class="star-item" v-for="(itemClass,k) in itemClasses" :key="k" :class="itemClass" ></span>
    </div>
  <!-- score 4.2
    4 4
    0.2 
    0.5 
   -->
</template>

<script>
    const LENGTH = 5;
    //  
    const ON = 'on';
    const OFF = 'off';
    const HALF = 'half';
    export default {
        props:[
            'score',
            'size'
        ],
        computed:{
            starType(){
                return 'star-'+ this.size;
            },
            itemClasses(){
                //
                let result = [];
                //  score 
                let integer = Math.floor(this.score);
                //  score
                let decimal = this.score - integer;
                //score
                for(let i=0;i<integer;i++){
                    result.push(ON);
                }
                //0.5
                if(decimal > 0.5){
                    result.push(HALF);
                }
                //52233buliang
                while(result.length < LENGTH){
                    result.push(OFF);
                }
                return result;
            }
        }
    }
</script>

<style lang="less" scoped>
    .star{
        &.star-small{
                .star-item{
                    display: inline-block;
                    width:0.3rem;
                    height: 0.3rem;
                    margin-right: 0.1rem;
                    &.on{
                        background-image: url('star_on.png');
                        background-size: 0.3rem 0.3rem;
                        background-repeat: no-repeat;
                    }
                    &.half{
                        background-image: url('star_half.png');
                        background-size: 0.3rem 0.3rem;
                        background-repeat: no-repeat;
                    }
                    &.off{
                        background-image: url('star_off.png');
                        background-size: 0.3rem 0.3rem;
                        background-repeat: no-repeat;
                    }
                }
            }
            .star-item{
                    display: inline-block;
                    width:0.48rem;
                    height: 0.48rem;
                    margin-right: 0.32rem;
                    &.on{
                        background-image: url('star_on.png');
                        background-size: 0.48rem 0.48rem;
                        background-repeat: no-repeat;
                    }
                    &.half{
                        background-image: url('star_half.png');
                        background-size: 0.48rem 0.48rem;
                        background-repeat: no-repeat;
                    }
                    &.off{
                        background-image: url('star_off.png');
                        background-size: 0.48rem 0.48rem;
                        background-repeat: no-repeat;
                    }
                }
        
        }

</style>




