import axios from 'axios';
import TYPES from '../types.js'

const state = {
        seller:{},
        sellerShow:false,
}

const getters = {
    seller(state){
        return state.seller;
    },
    sellerShow(state){
        return state.sellerShow;
    }

}

const actions = {
        //seller
        getSeller({ commit }){
            //ajax,configindex.js 
            axios.get('data.json').then(res => {
                // console.log(res.data.seller);
                console.log(res);
                commit(TYPES.GET_SELLER,res.data.seller);
            })
        },
        //sellerDetail
        showDetail({commit}){
            commit(TYPES.SHOW_DETAIL)
        },
        //sellerDetail
        hideDetail({commit}){
            commit(TYPES.HIDE_DETAIL)
        }
}

const mutations = {
        //seller,getters return,mapGetters
        [TYPES.GET_SELLER](state,data){
            state.seller = data;
        }, 
        //sellerDetail
        [TYPES.SHOW_DETAIL](state){
             //getters returnsellerDetailvshow
            state.sellerShow = true;
        },
        //sellerDetail
        [TYPES.HIDE_DETAIL](state){
            state.sellerShow = false;
        }
}

export default {
    state,
    getters,
    actions,
    mutations
}


